body{
    height: 100vh;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.navbar{
    height: 20vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow:  0 5px 5px rgba(0, 0, 0, 0.1);
}
.link-items{
    display: flex;
    align-items: center;
}
.link-items > li{
    list-style: none;
    margin: 40px;
}
.link-items >li>a{
    text-decoration: none;
    text-decoration-line: none;
    color: #666867;
    font-size: 1.3rem;
    font-weight: 600;
}
.logo{
    height: 60px;
    opacity: 1;
}
h1{
    text-align: center;
}
main{
    background: url('../src/logo.svg') no-repeat;
    background-position: right 50% bottom 50%; 
}
.facts-container{
    width: 80%;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #111;
}
.funFacts{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.funFacts > li{
    padding:10px;
    font-size: 1.4rem;
    color: #212223;
}
.funFacts > li::marker{
    font-size: 1.4rem;
    color: #676869;
}
footer{
    width: 100%;
    height: 10vh;
    background-color: #1a1a1a;
    position: fixed;
    bottom: 0;
    left: 0;
}
footer>p{
    color: ghostwhite;
    text-align: center;
} 

@media (min-width: 992px){
    .facts-container{
        width: 50%;
        margin: 40px auto;
    }
}

@media (max-width:586px){
    .link-items > li{
        list-style: none;
        margin: 10px;
    }
    .link-items >li>a{
        font-size: 1rem;
    }
    .logo{
        height: 40px;
    }    
}